module.exports = {
	qAll(...args) {
		if (args.length === 2) {
			return Array.from(args[0].querySelectorAll(args[1]));
		} else {
			return Array.from(document.querySelectorAll(args[0]));
		}
	},
	q(...args) {
		if (args.length === 2) {
			return args[0].querySelector(args[1]);
		} else {
			return document.querySelector(args[0]);
		}
	},
	gId(...args) {
		if (args.length === 2) {
			return args[0].getElementById(args[1]);
		} else {
			return document.getElementById(args[0]);
		}
	},

	alias(string) {
		if (typeof(string) == 'string') {
			string = string.toLowerCase();

			// xa du
			string = string
				.normalize('NFD') // chuyn chui sang unicode t hp
				.replace(/[\u0300-\u036f]/g, ''); // xa cc k t du sau khi tch t hp

			// Thay k t 
			string = string.replace(/[]/g, 'd');

			// Xa k t c bit
			string = string.replace(/([^0-9a-z-\s])/g, '');

			// Xa khong trng thay bng k t -
			string = string.replace(/(\s+)/g, '-');

			// Xa k t - lin tip
			string = string.replace(/-+/g, '-');

			// xa phn d -  u & cui
			string = string.replace(/^-+|-+$/g, '');

			// return
			return string;
		}
	},
}